#more-arrows{
  width: 70px;
  height: 60px;
  animation-name: polygon;
  animation-duration: 1s;
  animation-iteration-count: infinite;
}

@keyframes polygon {
  0% {opacity: 0;}
  50% {opacity:1}
  100% {opacity: 0;}
}

polygon {
  fill: rgb(228, 186, 81);
  transition: all .2s ease-out;
    
  &.arrow-middle {
	  opacity: 0.75;
	}
	&.arrow-top {
	  opacity: 0.5
	}
	 
