.card-container, .card, .card .side {
	width: 100%;
	height: 100%;
}
.card-container {
	cursor: pointer;
	perspective: 600px;
}

.card {
	position: relative;
	transition: transform .8s;
	transform-style: preserve-3d;
	box-shadow: 0 0 3px rgba(#bababa, .15);

	.side {
		-webkit-backface-visibility: hidden;
		backface-visibility: hidden;
	}
	.front {
    margin: 1%;
	}
	.back {
		transform: rotateY(180deg);
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background: linear-gradient(#e66465, #9198e5);
}}
    
.card-container:hover .card {
	// border: 2px solid #bababa;
	box-shadow: 0 0 10px rgba(#bababa, .6);

	.back::after {
		opacity: .5;
	}
}
.card-container:hover .card.flipped {
	border: 2px solid darken(#bababa, 5%);
}

.card.flipped {
	// border: 2px solid darken(#bababa, 10%);
	transform: rotateY(180deg);
}

